@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  box-sizing: border-box;
  margin: 0%;
  padding: 0%;
}

body {
  background-color: #111;
  font-family: "Montserrat", sans-serif;
  color: #fff; 
}

nav.DashNav {
  height: 50px;
  border-bottom: 1px solid #333;
  display: flex;
  justify-content: space-between;
}

nav.DashNav ul {
  display: flex;
  margin: 5px 0px;
}
nav.DashNav li {
  list-style: none;
  text-align: center;
  margin: 0px 5px;
  padding: 0%;
  font-size: 0.9rem;
  text-transform: uppercase;
}

nav.DashNav li a{
    color: #ffffff50;
}

nav.DashNav li a:hover{
    color: #ffffff;
    text-decoration: none;
}

nav.DashNav a {
  text-decoration: none;
  color: #fff;
  padding: 13px 10px;
  margin: 0px;
  line-height: 43px; 
  white-space:nowrap;
}

nav.DashNav button:hover {
text-decoration: underline;
}

nav.DashNav button {
  text-decoration: none;
  background-color: transparent;
  outline: none;
  border: none;
  color: #fff;
  padding: 13px 10px;
  margin: 0px;
  line-height: 43px; 
  white-space:nowrap;
  cursor: pointer;
}


nav.DashNav .logo {
  text-align: center;
  font-weight: bolder;
  font-size: 1.2rem;
  margin: 5px 0px;
  z-index: 10;
  background-color: #02c39a;
  /* background-color: #02c39a; */
  position: relative;
  border-radius: 0px 30px 30px 0px;
}

nav.DashNav .logo::before {
  content: "";
  position: absolute;
  right: -15px;
  top: 0%;
  height: 100%;
  width: 100px;
  background-color: #02c39a;
  z-index: -10;
  transform: skewX(-30deg);
}

nav.DashNav .logo span {
  color:#222;
}

nav.DashNav .profile {
    padding-right: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.9rem;
  margin: 5px 0px;

}

nav.DashNav .profile img{
    width: 25px;
    height: 25px;
    border-radius: 50px;
    background-color: #02c39a;
  }

main {
  display: flex;
  flex-direction: row;
  min-height: calc(100vh - 50px);
}

main aside:nth-child(1) {
  width: 55px;
  min-width: 55px;
  position: static;
  left: 0%;
  min-height: 100%;
  border-right: 1px solid #333;

}

main aside:nth-child(2) {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  flex-basis: 100%;
}

main .searchsec {
  border-bottom: 1px solid #333;
  position: relative;
  z-index: 0;
}

main .searchsec::after {
  content: "";
  position: absolute;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px) saturate(120%);
  -webkit-backdrop-filter: blur(4px) saturate(120%);
  background-color: rgba(17, 25, 40, 0.575);
  z-index: -1;
}
main .searchsec::before {
  content: "";
  position: absolute;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  filter: hue-rotate(90deg);
  background:  url("../Assests/abstractbg2.jpg");
  background-position: 50% 60%; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  z-index: -2;
}

main .searchsec .searchbar {
  padding: 2.5rem 10px;
  /* background-color: #02c39a; */
  display: flex;
  flex-direction: column;
  max-width: 800px;
  gap: 10px;
  margin: auto;
}

main .searchsec .searchbar h1 {
  text-align: center;
  width: 100%;
  font-size: 2rem;
  color: #ffffff50;
}
main .searchsec .searchbar h1 span{
  color: #fff;
  text-transform: uppercase;
}

main .searchsec .searchbar small{
  color: #ffffff50;
  text-align: center;
  width: 100%;
  padding-bottom: 20px;
}

main .searchsec .searchbar label {
  color: #ffffff50;
  font-size: 0.8rem;
  width: 100%;
}

main .searchres {
  border-top: 1px solid #333;
  padding: 15px;
}

main .searchres div {
  background-color: #22222270;
  display: flex;
  gap: 10px;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  margin: 10px 0px;
  cursor: pointer;
}

main .searchres div span:nth-child(1) {
  display: flex;
  padding: 5px 5px;
}

main .searchres div span p {
    font-size: 0.9rem;
}

main .searchres div span small {
  color: #ffffff50;
  font-size: 0.8rem;
}

main .searchres div img {
  width: 30px;
  height: 30px;
  border-radius: 50px;
}
  
main .searchres hr {
  height: 0;
  border: none;
  margin-top: 5px;
}

main .profilesec {
  /* border-left: 1px solid #333; */
  flex-basis: 75%;
}

.profilesec .profile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1c1c1c;
  padding: 0px;
  border-bottom: 1px solid #333;
}

.profilesec .profile > span > h1 {
  font-size: 2rem;
}
.profilesec .profile > span > h2 {
  font-size: 1rem;
  color: #ffffff50;
}

.profilesec .profile > span {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: left;
  gap: 5px;
}

.profilesec .profile > div {
  padding: 20px;
  text-align: right;
  display: flex;
  gap: 40px;
  padding-right: 50px;
}

.profilesec .profile > div > span small {
  color: #ffffff50;
  font-size: 0.8rem;
}

.profilesec .profilepic {
  width: 40px;
  border-radius: 40px;
  height: 40px;
  object-fit: cover;
}

.profilesec .Resultbtnbox {
  /* border-top: 1px solid #333; */
  padding: 15px;
}

.profilesec .Resultbtnbox ul {
  display: flex;
  /* margin-left: 20px; */
}

.profilesec .Resultbtnbox li {
  list-style: none;
}

.profilesec .Resultbtnbox li button {
  padding: 2px 10px;
  margin-right: 15px;
  display: block;
  height: 2rem;
  line-height: 2rem;
  text-decoration: none;
  text-transform: capitalize;
  font-size: 0.7rem;
  color: #ffffff50;
  border: none;
  background-color: #33333330;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
}

.profilesec .Resultbtnbox li button:hover {
  background-color: #33333350;
}

.profilesec .Resultbtnbox li button.active {
  /* border-bottom: 1px solid #fff; */
  color: #ffffff;
  background-color: #33333370;
}

.profilesec .list {
  margin: 15px;
  padding: 10px;
  margin-top: 0px;
  border-radius: 10px;
  /* border: 1px solid #333; */
  background-color: #33333320;
  /* overflow: hidden; */
}

.profilesec .list table {
  border-collapse: collapse;
  width: 100%;
  text-align: left;
  border-radius: 10px;
}

.profilesec .list table thead {
  border-bottom: 1px solid #333;
}

.profilesec .list table tr:last-of-type {
  border-bottom: 1px solid #333;
}

.profilesec .list table thead th {
  font-size: 0.8rem;
  color: #ffffff80;
  font-weight: normal;
  padding: 20px 10px;
  text-align:center;
}

.profilesec .list table thead th:nth-child(1) {
  width: 50px;
}
.profilesec .list table thead th:nth-child(2) {
  width: 25%;
  min-width: 200px;
  color: #02c39a;
  text-align:left;
}

.profilesec .list table thead th:nth-child(5) span{
    background-color: #ff140c10;
    color: #fff;
  }
  .profilesec .list table thead th:nth-child(4) span {
    background-color: #fffb0c10;
    color: #fff;
  }
  .profilesec .list table thead th:nth-child(3) span{
    background-color: #59FF0C10;
    color: #fff;
}

.profilesec .list table thead th:nth-child(5){
  border-right: 1px solid #333;
}

.profilesec .list table thead th:nth-child(3),
  .profilesec .list table thead th:nth-child(4),
.profilesec .list table thead th:nth-child(5) {
  padding: 0%;
  margin: 0%;
}

.profilesec .list table thead th span{
  border-radius: 5px;
  margin: 0px 5px;
  padding: 10px 15px;
    
}

.profilesec .list table tbody td:nth-child(2) {
  text-align:left;
}

.profilesec .list table tbody td:nth-child(2) > span {
  white-space: break-spaces;
  word-break: break-all;
  display: -webkit-box;
  -webkit-line-clamp:1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  cursor: pointer;
}
.profilesec .list table thead th:not(:nth-of-type(1))th:not(:nth-of-type(2))th:not(:nth-of-type(3))th:not(:nth-of-type(4)):not(:nth-of-type(5)) {
  width: 100%;
}
.profilesec .list table thead th:nth-of-type(12){
  width: 80px;
}

.profilesec .list table thead th:last-of-type{
  width: fit-content;
}

.profilesec .list table tbody td:last-of-type {
  border-right: none;
}

.profilesec .list table tbody tr:nth-child(2n) {
  /* border-bottom: 1px solid #333; */
  background-color: #33333330;
}

.profilesec .list table tbody tr:hover, .profilesec .list table thead tr:hover {
  background-color: #33333360;
}

.profilesec .list table tbody td {
  font-size: 0.8rem;
  color: #ffffff;
  font-weight: normal;
  padding: 12px 10px;
  position: relative;
  text-align:center ;
  border-right: 1px solid #33333380;
}

.profilesec .list table tbody td .wrap {
  position: absolute;
  width: 80%;
  height: 7px;
  border-radius: 10px;
  background-color: #ffffff50;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.profilesec .list table tbody td .box {
  position: absolute;
  height: 7px;
  border-radius: 10px;
  background-color: #02c39a;
  left: 0;
  top: 0;
}

.menu-nav.DashNav {
  background-color: #222;
  display: flex;
  justify-content: space-between;
}

.menu-item {
  color: #fcc;
  padding: 0px;
}

.three-dots:after {
  cursor: pointer;
  color: #fcc;
  content: "\22EF";
  font-size: 20px;
  padding: 8px;
  line-height: 10px;
}

.dropdowntd{
  padding: 0px;
}

.dropdown {
  background-color: #1c1c1c;
  min-width: 80px;
  outline: none;
  transition: opacity 0.1s, z-index 0.1s max-height 5s;
  border-radius: 2px;
  font-size: 0.8rem;
  text-decoration: none;
  color: #ffffff50;
  word-break: keep-all;
  white-space:nowrap;
  padding: 10px;
  border-bottom: 1px solid #333;
}

.dropdown:last-of-type {
  border-bottom: none;
}

.dropdown:hover {
  background-color: #222222;
  color: #ffffff;
}

.dotmenu-items {
  position: absolute;
  top: 10px; /* Adjust the position as needed */
  right: 50px;
  z-index: 10;
  display: flex;
  flex-direction: column;
  outline: 1px solid #333;
  border-radius: 5px;
}
 
/*-------------------------------- input button csss ---------------- */
.searchbar .input {
  font-size: 1rem;
  color: #fff;
  background-color:#1c1c1c;
  border-radius: 0.4vw;
  border: none;
  outline: none;
  padding: 10px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  flex-grow: 1;
}

.searchbar .input:hover {
  box-shadow: 0 0 0 1px #02c39a50;
}

.searchbar .input:focus {
  box-shadow: 0 0 0 1px #02c39a;
}

.searchbar .input:-webkit-autofill {
  transition: all 0s 50000s;
}

.searchsec .filter-wrapper{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.searchsec .filterBtn{
  display: flex;
  flex-direction: row;
  gap: 15px;
}

.searchsec .filterBtn > button {
  padding: 5px 20px;
  border-radius: 8px;
  width: fit-content;
  border: 0;
  display: block;
  text-transform: uppercase;
  font-size: 0.8rem;
  transition: all 0.5s ease;
  margin-top: 5px;
  cursor: pointer;
  background-color: #1c1c1c80;
  color: #ffffff50;
  outline: 1px solid transparent;
}


.searchsec .filterBtn > button:hover {
  background-color: #1c1c1c;
}

.searchsec .filterBtn > button.active{
  background-color: #1c1c1c;
  color: #fff;
}

.searchsec button.startBtn {
  padding: 8px 20px;
  border-radius: 50px;
  width: fit-content;
  border: 0;
  display: block;
  text-transform: uppercase;
  font-size: 0.8rem;
  transition: all 0.5s ease;
  margin-top: 5px;
  cursor: pointer;
  background-color: #02c39a;
  color: #fff;
  box-shadow: #02c39a50 0px 2px 9px 0px;
}

.searchsec button.startBtn:hover {
  background-color: #02c39a;
  color: #fff;
  box-shadow: #02c39a 0px 1px 5px 0px;
}

.searchsec button.startBtn:active {
  box-shadow: #02c39a 0px 1px 3px 0px;
  transition: 100ms;
  letter-spacing: 1px;
}

