.temp table {
    border-spacing: 1;
    border-collapse: collapse;
    background: rgba(78, 78, 78, 0.39);
    border-radius: 6px;
    overflow: hidden;
    max-width: 800px;
    width: 100%;
    margin: 20px;
    position: relative;
}
.temp table * {
    position: relative;
}
.temp table td, .temp table th {
    padding: 12px;

}
.temp table thead  {
    background: #444444;
}
.temp table thead tr {
    height: 60px;
    font-size: 16px;
}
.temp table tbody tr {
    height: 48px;
    border-bottom: 1px solid #202020;
}
.temp table tbody tr:last-child {
    border: 0;
}
.temp table td, .temp table th {
    text-align: left;
}
.temp table td.l, .temp table th.l {
    text-align: right;
}
.temp table td.c, .temp table th.c {
    text-align: center;
}
.temp table td.r, .temp table th.r {
    text-align: center;
}
@media screen and (max-width: 35.5em) {
    .temp table {
        display: block;
   }
    .temp table > *, .temp table tr, .temp table td, .temp table th {
        display: block;
   }
    .temp table thead {
        display: none;
   }
    .temp table tbody tr {
        height: auto;
        padding: 8px 0;
   }
    .temp table tbody tr td {
        padding-left: 45%;
        margin-bottom: 12px;
   }
    .temp table tbody tr td:last-child {
        margin-bottom: 0;
   }
    .temp table tbody tr td:before {
        position: absolute;
        font-weight: 700;
        width: 40%;
        left: 10px;
        top: 0;
   }
    .temp table tbody tr td:nth-child(1):before {
        content: "Code";
   }
    .temp table tbody tr td:nth-child(2):before {
        content: "Stock";
   }
    .temp table tbody tr td:nth-child(3):before {
        content: "Cap";
   }
    .temp table tbody tr td:nth-child(4):before {
        content: "Inch";
   }
    .temp table tbody tr td:nth-child(5):before {
        content: "Box Type";
   }
}

